@import url("../../Style/Fonts.css");

header{
    background-color: black;
    width: 94vw;
    display:inline-flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 1vh;
    padding-left: 3vw;
    padding-right: 3vw;
    position: fixed;
    top:0;
    z-index: 100;
}
span{
    flex: 0 0 50%;
    text-align: left;
}

.button_group{
    display: flex;
    flex: 0 0 20%;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: nowrap;
}

.setting_icon{
    height: 6vh;
    margin-bottom: -0.5vh;
}
.home_icon{
    height: 5vh;
}

@media (max-width: 500px) {
    span{
        flex: 0 0 100%;
        text-align: center;
        align-items: center;
        width: 100%;
    }
    .button_group{
        display: flex;
        flex: 0 0 100%;
        width: 100%;
        flex-wrap: nowrap;
        
    }
    .setting_icon{
        height: 5vh;
        margin-bottom: -0.5vh;
    }
    .home_icon{
        height: 4vh;
    }
}